import { Constants } from '../core/constants';
import { HttpSuccesFailureResponse } from '../core/http_wrapper/http_wrapper_response';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OnDestroy } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ValidationService } from '../common/validationService/validation.service';
import { HttpUserUtilsService } from "../core/http_wrapper/http-user-utils.service";
import { Subscription } from "rxjs";
import { Observable } from "rxjs/Observable";
declare var $: any;


@Component({
  selector: 'app-resetpassword',
  templateUrl: './resetpassword.component.html',
  styleUrls: ['./resetpassword.component.css']
})
export class ResetpasswordComponent implements OnInit,OnDestroy,HttpSuccesFailureResponse {
  private token:any;
  private sub:any;
  resetPasswordForm:FormGroup;
  private subscription: Subscription;
  private timer: Observable<any>;
  changepassword:boolean=false;
  errorMessage: string = "";
  constructor(private route: ActivatedRoute,private router:Router,private formBuilder: FormBuilder,private http_user:HttpUserUtilsService) { }
          ngAfterViewInit(){

            $('#newpassword').bind("cut copy",function(e) {
     e.preventDefault();
 });
            $('#confirmpassword').bind("cut copy",function(e) {
     e.preventDefault();
 });
   }
  ngOnInit() {
    this.resetPasswordForm=this.formBuilder.group({
      newpassword:['',Validators.compose([Validators.required,Validators.minLength(6),Validators.maxLength(20)])],
      confirmpassword:['',Validators.compose([Validators.required,Validators.minLength(6),Validators.maxLength(20)])]
    },
  {
    validator: ValidationService.checkIfMatchingPasswords('newpassword', 'confirmpassword')
  });
    this.sub=this.route.queryParams.subscribe(params => {
      this.token=params['token']||null;
      if(this.token == null){
      this.router.navigate(['/']);
      }
    else{ 
    }
    });

  }
  onSave(){
    if(this.resetPasswordForm.valid){
    this.http_user.resetpassword(this.resetPasswordForm.value.newpassword,this.resetPasswordForm.value.confirmpassword,this.token,this,true)
    }
  }
  onCancel(){
    this.router.navigate(['/'])
  }
  private showAlert(value){
      this.changepassword=value;
      this.timer = Observable.timer(5000); // 5000 millisecond means 5 seconds

      this.subscription = this.timer.subscribe(() => {
       this.changepassword=false;
      this.router.navigate(['/'])
    });

  }
  showError(message: string) {
    this.errorMessage = message;
    document.getElementById('errorMessageContainer').style.opacity = "1.0";
    setTimeout(() => {
      this.hideError();
    }, 3000);
  }

  hideError() {
    document.getElementById('errorMessageContainer').style.opacity = "0.0";
    this.errorMessage = '';
  }
 ngOnDestroy() {
    this.sub.unsubscribe();
  }
  onSuccess(type: any, responsedata: any) {
    if(type == Constants.verifypasswd){
      this.showAlert(true);
    }

    }
  onFailure(type: any, response: string) {
    if(type == Constants.verifypasswd){
      this.changepassword=false;
      this.showError(response)
    }
    }
}



